<template>
    <v-ons-toolbar id='toto'>
      <div class="center" style="width: 100%">
        <v-ons-if platform="android">Onsen UI Jukebox</v-ons-if>
        <v-ons-if platform="ios other">{{toolbarTitle}}</v-ons-if>
      </div>
      <v-ons-if class="right" platform="android">
        <v-ons-toolbar-button ng-click="androidSearchModal.show(); ">
          <v-ons-icon icon="md-search"></v-ons-icon>
        </v-ons-toolbar-button>
      </v-ons-if>
    </v-ons-toolbar>
</template>

<script>
export default {
  name: 'toolBar',
  data () {
    return {
    	toolbarTitle: 'test titre',
    	platform : ''
    }
  },
  methods: {
  },
  beforeCreate() {
    console.log('Android:'+this.$ons.platform.isAndroid());
    console.log('IOS:'+this.$ons.platform.isIOS());
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



